<template>
  <div class="advertsing">
    <el-form
      :model="ruleForm"
      :rules="rules"
      ref="ruleForm"
      label-width="100px"
      class="demo-ruleForm"
    >
      <el-form-item label="主题" prop="theme">
        <el-input v-model="ruleForm.theme"></el-input>
      </el-form-item>
      <el-form-item label="截止时间" required>
        <el-col :span="11">
          <el-form-item prop="date1">
            <el-date-picker
              type="date"
              placeholder="选择日期"
              v-model="ruleForm.date1"
              style="width: 100%"
            ></el-date-picker>
          </el-form-item>
        </el-col>
        <el-col class="line" :span="2">-</el-col>
        <el-col :span="11">
          <el-form-item prop="date2">
            <el-time-picker
              placeholder="选择时间"
              v-model="ruleForm.date2"
              style="width: 100%"
              @blur="timeChange"
            ></el-time-picker>
          </el-form-item>
        </el-col>
      </el-form-item>
      <el-form-item label="广告描述" prop="content">
        <el-input type="textarea" v-model="ruleForm.content"></el-input>
      </el-form-item>
      <el-form-item label="广告图片" prop="ADimg" style="max-height: 100px">
        <el-upload
          action="http://localhost:8081/img"
          list-type="picture-card"
          :auto-upload="false"
          ref="MyUpload"
          :limit="1"
          :data="{
            id: this.flag,
          }"
          title="仅允许上传一张图片"
          style="height: 100px"
        >
          <i slot="default" class="el-icon-plus" style="height: 100px"></i>
          <div slot="file" slot-scope="{ file }">
            <img
              class="el-upload-list__item-thumbnail"
              :src="file.url"
              alt=""
              style="height: 100px"
            />
            <span class="el-upload-list__item-actions">
              <span class="el-upload-list__item-status-text">
                <i class="el-icon-download"></i>
              </span>
              <span
                v-if="!disabled"
                class="el-upload-list__item-delete"
                @click="handleRemove(file)"
              >
                <i class="el-icon-delete"></i>
              </span>
            </span>
          </div>
        </el-upload>
        <el-dialog :visible.sync="dialogVisible">
          <img width="100%" :src="dialogImageUrl" alt="" />
        </el-dialog>
      </el-form-item>
      <!-- 价格 -->
      <el-form-item label="价格" prop="price">
        <el-input
          style="width: 50%; color: #ffce45; margin-left: -190px"
          v-model="price"
          disabled
        />
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')"
          >立即创建</el-button
        >
        <el-button @click="resetForm('ruleForm')">重置</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: "Advertising",

  data() {
    return {
      userForm: this.$store.getters.getUser,
      ruleForm: {
        ownerID: this.$store.getters.getUser.id,
      },
      dialogImageUrl: "",
      dialogVisible: false,
      disabled: false,
      price: 0,
      rules: {
        theme: [
          { required: true, message: "请输入广告主题", trigger: "blur" },
          {
            min: 1,
            max: 10,
            message: "长度在 1 到 10 个字符",
            trigger: "blur",
          },
        ],
        date1: [
          {
            type: "date",
            required: true,
            message: "请选择日期",
            trigger: "change",
          },
        ],
        date2: [
          {
            type: "date",
            required: true,
            message: "请选择时间",
            trigger: "change",
          },
        ],
        content: [
          { required: true, message: "请填写广告描述", trigger: "blur" },
        ],
      },
      flag: "",
    };
  },

  mounted() {},

  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          var date_1 = this.$moment(this.ruleForm.date1).format("YYYY-MM-DD");
          var date_2 = this.$moment(this.ruleForm.date2).format("HH:mm:ss");
          this.ruleForm.date = date_1 + " " + date_2;
          this.ruleForm.price = this.price;
          this.$axios.post("/advertising", this.ruleForm).then((res) => {
            if (res.data.code == 200) {
              this.$message.success("申请成功");
              this.flag = res.data.data.id;
              this.$refs["MyUpload"].submit();
              this.resetForm("ruleForm");
            } else {
              this.$message.error(res.data.msg);
            }
          });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
      this.$refs["MyUpload"].clearFiles();
    },
    handleRemove(file) {
      this.$refs["MyUpload"].clearFiles();
    },
    timeChange() {
      // 获取当前时间
      var date1 = this.$moment(this.ruleForm.date1).format("YYYY-MM-DD");
      var dateTime = this.DateDiffer(date1);
      this.price = dateTime * 100;
    },
    DateDiffer(Date_end) {
      //date1结束时间
      let date1 = new Date(Date_end);
      //date2当前时间
      let date2 = new Date();
      date1 = new Date(date1.getFullYear(), date1.getMonth(), date1.getDate());
      date2 = new Date(date2.getFullYear(), date2.getMonth(), date2.getDate());
      const diff = date1.getTime() - date2.getTime(); //目标时间减去当前时间
      const diffDate = diff / (24 * 60 * 60 * 1000); //计算当前时间与结束时间之间相差天数
      return diffDate;
    },
  },
};
</script>

<style lang="less" scoped>
.advertsing {
  width: 100%;
  height: 100%;
  border-radius: 15px;
  background-color: #fff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
}
/deep/.el-form {
  padding: 50px 20px;
  width: 40%;
  height: 90%;
}
/deep/.el-upload--picture-card {
  margin-left: -280px;
  height: 100px;
  width: 100px;
  line-height: 110px;
}
</style>